  scroll-behavior: smooth;
}

body {
  background-color: white;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.is-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  position: relative;
  padding: 16px 0 25px 0;
}
.header_container {
  max-width: 1170px;
  padding: 0 15px;
  margin: 0 auto;
}
@media (min-width: 1440px) {
  .header_container {
    max-width: 1440px;
  }
}
.header_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_logo {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header_menu {
  display: flex;
  gap: 20px;
}
@media (max-width: 1051px) {
  .header_menu {
    display: none;
  }
}
.header_contact {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 13px 0;
  width: 138px;
  border: 1px solid #333333;
  border-radius: 2px;
  background: #000;
  transition: background-color 0.4s ease;
}
.header_contact_link {
  font-family: "DM Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  transition: color 0.5s ease;
}
.header_contact:hover {
  background-color: #fff;
}
.header_contact:hover .header_contact_link {
  color: black;
}

.menu_link {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 200%;
  text-align: right;
  color: black;
  text-transform: capitalize;
  transition: text-shadow 0.5s ease;
}
.menu_link:hover, .menu_link:focus {
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (min-width: 1440px) {
  .menu_link {
    font-size: 23px;
  }
}

.burger-menu {
  cursor: pointer;
  height: 27px;
  width: 27px;
  overflow: visible;
  position: relative;
  z-index: 20;
}
@media (min-width: 1051px) {
  .burger-menu {
    display: none;
  }
}
.burger-menu span,
.burger-menu span:before,
.burger-menu span:after {
  z-index: 20;
  background: black;
  display: block;
  height: 2px;
  opacity: 1;
  position: absolute;
  transition: 0.3s ease-in-out;
}
.burger-menu span:before,
.burger-menu span:after {
  content: "";
}
.burger-menu span:before {
  left: 0px;
  top: -10px;
  width: 27px;
}
.burger-menu span {
  right: 0px;
  top: 13px;
  width: 27px;
}
.burger-menu span:after {
  left: 0px;
  top: 10px;
  width: 27px;
}
.burger-menu.close span:before {
  top: 0px;
  transform: rotate(90deg);
  width: 27px;
}
.burger-menu.close span {
  transform: rotate(-45deg);
  top: 13px;
  width: 27px;
}
.burger-menu.close span:after {
  top: 0px;
  left: 0;
  transform: rotate(90deg);
  opacity: 0;
  width: 0;
}

.mobile-menu {
  z-index: 1;
  position: fixed;
  width: 100vw;
  height: 100%;
  background-color: black;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
}
.mobile-menu_list {
  z-index: 2;
  padding-top: 160px;
  display: flex;
  flex-direction: column;
  gap: 35px;
}
.mobile-menu_link {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 32px;
  text-transform: capitalize;
  line-height: 111%;
  color: #fff;
}
.mobile-menu_link:hover, .mobile-menu_link:focus {
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.mobile-menu_bg {
  position: absolute;
  bottom: 50%;
  left: 30%;
}
@media (max-width: 768px) {
  .mobile-menu_bg {
    left: 36%;
  }
}

.hero {
  background-color: #E7D4BE;
}
.hero_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-items: normal;
}
@media (max-width: 485px) {
  .hero_wrapper {
    flex-direction: column-reverse;
  }
}
@media (min-width: 1440px) {
  .hero_wrapper {
    justify-content: center;
  }
}
.hero_content {
  position: relative;
  padding: 162px 123px 241px 123px;
  margin: 0 auto;
}
@media (min-width: 1440px) {
  .hero_content {
    margin: unset;
  }
}
@media (max-width: 768px) {
  .hero_content {
    padding: 42px 113px 0 23px;
  }
}
@media (max-width: 485px) {
  .hero_content {
    padding: 30px 60px;
  }
}
.hero_subtext {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.19em;
  text-transform: uppercase;
  color: #000;
  margin-bottom: 21px;
}
.hero_subtext::before {
  content: url(../images/hero_grain.png);
  top: -50px;
  position: absolute;
  left: 0;
}
@media (max-width: 768px) {
  .hero_subtext {
    margin-bottom: 18px;
  }
}
.hero_title {
  font-family: "Ultra", serif;
  text-transform: capitalize;
  font-weight: 400;
  font-size: 72px;
  line-height: 111%;
  color: #000;
  width: 467px;
  position: relative;
  margin-bottom: 27px;
}
.hero_title::after {
  content: url(../images/hero-black-dots.png);
  top: -15%;
  position: absolute;
  right: 40%;
}
@media (max-width: 768px) {
  .hero_title {
    font-size: 48px;
    width: 230px;
  }
  .hero_title::after {
    top: -15%;
    right: -15%;
  }
}
@media (max-width: 485px) {
  .hero_title {
    width: 320px;
    font-size: 38px;
  }
  .hero_title::after {
    top: -65%;
    right: 0;
  }
}
.hero_description {
  font-family: "DM Sans", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 160%;
  color: #000;
  max-width: 470px;
}
@media (max-width: 768px) {
  .hero_description {
    font-size: 16px;
  }
}
.hero-img_item {
  max-width: 715px;
  max-height: auto;
}
@media (max-width: 768px) {
  .hero-img_item {
    max-width: 430px;
    margin: 0 auto;
  }
}

.about-us {
  padding: 160px 0;
}
.about-us_container {
  max-width: 1170px;
  padding: 0 15px;
  margin: 0 auto;
}
@media (min-width: 1440px) {
  .about-us_container {
    max-width: 1440px;
  }
}
.about-us_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-items: start;
  gap: 52px;
}
.about-us_img {
  width: 520px;
  display: flex;
  justify-content: center;
  align-items: end;
  height: 700px;
  background-image: url(../images//about-orange-bg.png);
  background-repeat: no-repeat;
  background-position: 0 192px;
  position: relative;
}
.about-us_img::before {
  content: url(../images/about-bread.png);
  width: 370px;
  position: absolute;
  top: 45px;
  left: -25px;
  z-index: 0;
}
.about-us_img .img_main {
  z-index: 1;
}
.about-us_content {
  padding-top: 45px;
}
.about-us_title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.19em;
  text-transform: uppercase;
  color: #171b1b;
  margin-bottom: 18px;
}
.about-us_subtitle {
  font-family: "Ultra", serif;
  font-weight: 400;
  font-size: 56px;
  line-height: 114%;
  color: #171b1b;
  text-transform: capitalize;
  margin-bottom: 59px;
  max-width: 570px;
}

.timeline {
  padding: 50px 0;
  max-width: 370px;
  margin: 0 auto;
}

.slick-prev.slick-arrow,
.slick-next.slick-arrow {
  position: absolute;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slick-prev.slick-arrow {
  left: -60px;
  top: -15px;
}

.slick-next.slick-arrow {
  left: -60px;
  bottom: 0;
}

.slick-next.slick-arrow::before {
  transform: rotate(180deg);
}

.slick-next.slick-arrow::before, .slick-prev.slick-arrow::before {
  content: "";
  display: block;
  width: 12px;
  height: 12px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 1;
  background-image: url("../../assets/images/arrow.png");
}

.slick-next {
  top: 60%;
}